
import AddOn from './TournamentRegistrationAddOn'

export default class TournamentRegistrationWindow {
  constructor (dto) {
    this.id = 0
    this.fee = null
    this.feeIsPerTeam = true
    this.startDate = null
    this.startDateFormatted = null
    this.startTime = null
    this.endDate = null
    this.endDateFormatted = null
    this.endTime = null
    this.isEarly = false
    this.isLate = false
    this.canPayAtEvent = true
    this.canProcessPayment = false
    // this.isCurrent = false
    // this.open = false

    this.addOn = new AddOn()

    if (dto) {
      this.update(dto)
    }
  }

  update (dto) {
    Object.assign(this, dto)
  }
  updateFromDummy (dummy) {
    this.fee = dummy.fee
    this.feeIsPerTeam = dummy.feeIsPerTeam
    this.startDate = dummy.startDate
    this.startDateFormatted = dummy.startDateFormatted
    this.startTime = dummy.startTime
    this.endDate = dummy.endDate
    this.endDateFormatted = dummy.endDateFormatted
    this.endTime = dummy.endTime
    this.isEarly = dummy.isEarly
    this.isLate = dummy.isLate
    this.canPayAtEvent = dummy.canPayAtEvent
    this.canProcessPayment = dummy.canProcessPayment
  }
  get feeString () {
    return `$ ${parseFloat(this.fee).toFixed(2)}/${this.feeIsPerTeam ? 'per team' : 'per player'}`
  }
  get isCurrent () {
    return true
  }
  get selectPayment () {
    return this.canPayAtEvent && this.canProcessPayment
  }
}



